import { createContext, useContext, useState, useEffect } from "react";
import {ZOHO} from '../assets/Zoho';
import {geocodeByAddress,geocodeByPlaceId,getLatLng} from 'react-places-autocomplete';


const ZohoContext = createContext();

export default function ZohoProvider({children}) {

   const [module, setModule] = useState(null);
   const [entity, setEntity] = useState(null);
   const [record, setRecord] = useState({});
   const [latylng, setLatyLng]=useState(null);


   useEffect(() => {

      const initWidget = () => {
         ZOHO.embeddedApp.on("PageLoad", function (data) {
            console.log(data);
            setModule(data.Entity);
            setEntity(data.EntityId);
            ZOHO.CRM.API.getRecord({Entity:data.Entity,RecordID:data.EntityId})
            .then(function(data){
                //console.log(data.data[0])
                setRecord(data.data[0])
                console.log("geocode")
               geocodeByAddress(data.data[0].Direccion)
               .then(results => getLatLng(results[0]).then(({ lat, lng }) =>
               // console.log('Successfully got latitude and longitude',  lat, lng ),
                  setLatyLng({ lat, lng }),
                  console.log(results)
               ))
                  
               .catch(error => console.error(error));
            })
             
         });

         ZOHO.embeddedApp.init();
      }

      setTimeout(()=> {
         initWidget();
      }, 1000);
      
   }, []);
   
   return (
      <ZohoContext.Provider value={{module: module, entity: entity, record:record, latylng: latylng}}>
         {children}
      </ZohoContext.Provider>
   );
};

export const useWidget = () => {

   const context = useContext(ZohoContext);

   if (!context) throw new Error("UseWidget inside ZohoContext");

   const { entity, module, record, latylng } = context;

   return {
      entity, module, record, latylng
   }
}